@import url(https://fonts.googleapis.com/css2?family=Catamaran&family=Roboto&display=swap);
@import url(https://pro.fontawesome.com/releases/v5.10.0/css/all.css);
@import url(https://fonts.googleapis.com/css2?family=Catamaran&family=Roboto&display=swap);
@import url(https://pro.fontawesome.com/releases/v5.10.0/css/all.css);
#NavbarBlock{
    background-color: white;
    height: 70px;
    box-shadow: 1px 0px 4px 0px gray;
    width: 100%;
    position: fixed;
}
#NavbarBlock article{
    display: flex;
    align-items: center;
    height: 70px;
    padding-left: 8%;
    padding-right: 8%;
}
.logo{
    width: 8%;
}
.menu{
    width: 35%;
}
.search{
    width: 41%;
}
.auth{
    width: 16%;
    display: flex;
    justify-content: space-between;
}
.profile a{
    text-decoration: none;
    color: black;
}
.profile span{
    font-family: sans-serif;
    margin-left:5px ;
}
.menu ul{
    list-style: none;
    display: flex;
    justify-content: space-around;
}
.menu ul li a{
    
    text-decoration: none;
    font-size: 14px;
    font-weight: 500;
    color: rgb(45, 45, 45);
    font-family: sans-serif;
}
#searchText input{
    background-color: whitesmoke;
    width: 90%;
    padding: 10px 40px;
    margin: 0 10px;
    border: none;
    border-radius: 4px;
}
#searchText span{
    position: relative;
}
#searchText span i{
    position: absolute;
    left:20px;
    right: 0px;
    top:2px;
    bottom: 0px;

}
.footer{
    height:400px ;
    width: 100%;
    background-color: #FAFBFC;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.Footer-sec{
    display: flex;
    flex-direction: row;
    margin-left: 10%; 
    width: 100%; 
    margin-right: 8%;
    justify-content: space-around;
    
}
.Onlineshopping1{
    width: 15%;
    padding-left: 65px;
}
.Usefullinks1{
    width: 10%;
}
.links{
    width: 100%;
}
.links a{
    text-decoration: none;
    color: gray;
    line-height: 1.6;
}
.Experienceapp1{
    width: 20%;

}
.playstore{
    display: flex;
    margin-top: 10px;
}
.plystr img{
    width: 150px;
}
.plystr2 img{
    width: 130px;
}
.Warranty1{
    width: 25%;
}
.links2{
    margin-top: 10px;
}
.Socilico{
    display: flex;
    padding-top: 5px;
    
}
.plystr i{
    margin-left: 15px;
}
.original{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.original .txt{
    margin-left: 20px;
}
.return{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.return .txt{
    margin-left: 20px;
}
.deals-text{
    height: 80px;
    display: flex;
    padding-left: 4%;
}
.deals-text h1{
    font-family: 'Roboto', sans-serif; 
    color: #201E1C;
}
.deals-image{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    padding-left: 7%;
    padding-right: 7%;
}
.deals-image img{
    width: 230px;
    height: 100%;
    
}
.ctb-text{
    height: 80px;
    display: flex;
    padding-left: 4%;
}
.ctb-text h1{
    font-family: 'Roboto', sans-serif; 
    color: #201E1C;
}
.ctb-image{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    padding-left: 5%;
    padding-right: 5%;
}
.ctb-image img{
    width: 200px;
    height: 100%;
    
}

.slider-img{
    display: flex;
    padding-top: 4%;
    padding-bottom: 2%;
    justify-content: center;   
}
*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}
body{
    
    font-size: 14px;
    font-family: 'Roboto', sans-serif;    
}



